<template>
  <div>
    <sc-sample-box>
      <template v-slot:description>this.$util.alert('')</template>
      <div>
        <sc-button @click="showAlert"></sc-button>
      </div>

      <sc-code-highlight
        template='<sc-button @click="showAlert"></sc-button>'
        script="
export default {
  methods: {
    showAlert() {
      this.$util.alert('.');
    },
  },
};"
      />
    </sc-sample-box>

    <sc-sample-box title="callback ">
      <template v-slot:description
        >       .</template
      >
      <div>
        <sc-button @click="showAlertWithCallbackFn"></sc-button>
        <div class="mt-5 text-sm"> : {{ result }}</div>
      </div>

      <sc-code-highlight
        template=' <div>
  <sc-button @click="showAlertWithCallbackFn"></sc-button>
  <div class="mt-5 text-sm"> :  {{ result }}</div>
</div>'
        script="export default {
  data() {
    return {
      result: '',
    };
  },
  methods: {
    showAlertWithCallbackFn() {
      this.result = 'OPEN';
      this.$util.alert('', () => {
        console.info('      ');
        this.result = 'CLOSE';
      });
    },
  },
};"
      />
    </sc-sample-box>

    <sc-sample-box title="alert Title">
      <template v-slot:description>String   Alert Title   .</template>
      <div>
        <sc-button @click="showAlertWithCallbackFn"></sc-button>
        <div class="mt-5 text-sm"> : {{ result }}</div>
      </div>

      <sc-code-highlight
        template='<div>
  <sc-button @click="showAlertWithCallbackFn"></sc-button>
  <div class="mt-5 text-sm"> : {{ result }}</div>
</div>'
        script=" export default {
  data() {
    return {
      result: '',
    };
  },
  methods: {
    showAlertWithCallbackFn() {
      this.result = 'OPEN';
      this.$util.alert('', 
        () => {
          console.info('      ');
          this.result = 'CLOSE';
        }, 
        'ALERT TITLE',
      );
    },
  },
};"
      />
      <div>
        <sc-button @click="showAlertWithTitle"></sc-button>
      </div>

      <sc-code-highlight
        template='
<div>
  <sc-button @click="showAlertWithTitle"></sc-button>
</div>'
        script="export default {
  methods: {
    showAlertWithTitle() {
      this.$util.alert('', 'ALTER TITLE');
    },
  },
};"
      />
    </sc-sample-box>
  </div>
</template>

<script>
import ScSampleBox from '@/components/common/ScSampleBox.vue';
import ScButton from '@/components/common/ScButton.vue';
import ScCodeHighlight from '@/components/common/ScCodeHighlight.vue';

export default {
  name: 'SampleAlert',
  components: { ScSampleBox, ScButton, ScCodeHighlight },
  data() {
    return {
      result: '',
    };
  },
  methods: {
    showAlert() {
      this.$util.alert('');
    },
    showAlertWithCallbackFn() {
      this.result = 'OPEN';
      this.$util.alert(
        '',
        () => {
          console.info('      ');
          this.result = 'CLOSE';
        },
        'ALERT TITLE',
      );
    },
    showAlertWithTitle() {
      this.$util.alert('', 'ALERT TITLE');
    },
  },
};
</script>

<style scoped lang="scss"></style>
