<template>
  <teleport to="body">
    <div
      v-if="isOpen"
      :class="[
        'sc-modal',
        'z-50',
        {
          transparent,
          'is-alert': isAlert,
          'no-resize': noResize,
          'no-drag': noDrag,
          'no-scroll': noScroll,
          'blue-tit': blueTitle,
        },
      ]"
    >
      <div
        ref="dragContainer"
        class="sc-modal-container"
        :class="['sc-modal-container', size, { 'shadow-xl': transparent, transparent }]"
        :style="customSize"
        @mousedown="startDrag"
        @mouseup="stopDrag"
        @mousemove="onDrag"
      >
        <!-- -->
        <div class="sc-modal-head drag-handle" @dblclick="toggleFullscreen">
          <span class="text-xl font-bold drag-handle">{{ title }}</span>
          <div class="flex items-center">
            <button @click="toggleFullscreen" class="flex mr-3">
              <sc-icon
                :type="isFullscreen ? 'rstr-btn' : 'mx-btn'"
                color="dark-gray"
                custom-size="19"
              />
            </button>
            <button v-show="closeBtnShow" @click="clickCloseIcon" class="flex">
              <sc-icon type="close_01" color="dark-gray" custom-size="18" />
            </button>
          </div>
        </div>
        <!-- -->
        <div class="sc-modal-body" :class="{ 'padding-bottom': !slots.action }">
          <slot :open="open" :close="close"></slot>
        </div>
        <!--  -->
        <div class="sc-modal-foot" v-if="slots.action">
          <slot name="action"></slot>
        </div>
        <!--   -->
        <div class="sc-modal-drag-handler"></div>

        <!--   -->
        <div class="resize-handle top" @mousedown="(event) => startResize(event, 'top')"></div>
        <div class="resize-handle left" @mousedown="(event) => startResize(event, 'left')"></div>
        <div
          class="resize-handle bottom"
          @mousedown="(event) => startResize(event, 'bottom')"
        ></div>
        <div class="resize-handle right" @mousedown="(event) => startResize(event, 'right')"></div>
        <div
          class="resize-handle bottom-right"
          @mousedown="(event) => startResize(event, 'bottomRight')"
        ></div>
        <div
          class="resize-handle top-left"
          @mousedown="(event) => startResize(event, 'topLeft')"
        ></div>
        <div
          class="resize-handle bottom-left"
          @mousedown="(event) => startResize(event, 'bottomLeft')"
        ></div>
        <div
          class="resize-handle top-right"
          @mousedown="(event) => startResize(event, 'topRight')"
        ></div>
      </div>
      <!--//   -->
    </div>
  </teleport>
</template>
<script>
import { useSlots } from 'vue';
import ScIcon from '@/components/common/ScIcon.vue';

export default {
  name: 'ScModal',
  components: { ScIcon },
  emits: ['opened', 'closed', 'closeIcon'],
  props: {
    title: {
      type: String,
      default: '',
    },
    size: {
      type: String,
      default: '',
      validator: (value) => ['', 'full', 'large', 'medium', 'small'].includes(value),
    },
    width: {
      type: [String, Number],
      default: null,
    },
    height: {
      type: [String, Number],
      default: null,
    },
    transparent: {
      type: Boolean,
      default: false,
    },
    noDrag: {
      type: Boolean,
      default: false,
    },
    noResize: {
      type: Boolean,
      default: false,
    },
    isAlert: {
      type: Boolean,
      default: false,
    },
    closeBtnShow: {
      type: Boolean,
      default: true,
    },
    noScroll: {
      type: Boolean,
      default: false,
    },
    blueTitle: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const slots = useSlots();
    return {
      slots,
    };
  },
  data() {
    return {
      isFullscreen: false,
      originalSize: {
        width: null,
        height: null,
        top: null,
        left: null,
      },
      isOpen: false,
      isDragging: false,
      isResizing: false,
      resizeDirection: '',
      startWidth: 0,
      startHeight: 0,
      startX: 0,
      startY: 0,
      dragPosition: {
        x: 0,
        y: 0,
      },
    };
  },
  computed: {
    customSize() {
      const style = {};
      if (this.isFullscreen) {
        style.width = '100vw';
        style.height = '100vh';
      } else {
        if (this.$util.isNotEmpty(this.width)) {
          style.width = `${this.width}`.endsWith('px') ? this.width : `${this.width}px`;
        }
        if (this.$util.isNotEmpty(this.height)) {
          style.height = `${this.height}`.endsWith('px') ? this.height : `${this.height}px`;
        }
      }
      return style;
    },
  },
  methods: {
    //  toggle
    toggleFullscreen() {
      const container = this.$refs.dragContainer;

      if (!this.isFullscreen) {
        //    
        this.originalSize = {
          width: container.style.width,
          height: container.style.height,
          top: container.style.top,
          left: container.style.left,
        };

        //  
        container.style.width = '100vw';
        container.style.height = '100vh';
        container.style.top = '0';
        container.style.left = '0';

        // body  
        document.body.style.overflow = 'hidden';
      } else {
        //   
        container.style.width = this.originalSize.width;
        container.style.height = this.originalSize.height;
        container.style.top = this.originalSize.top;
        container.style.left = this.originalSize.left;

        // body  
        document.body.style.overflow = 'visible';
      }

      this.isFullscreen = !this.isFullscreen;
    },
    open() {
      this.$emit('opened');
      this.isOpen = true;
    },
    clickCloseIcon() {
      this.$emit('closeIcon');
      this.$nextTick(() => {
        this.close();
      });
    },
    close() {
      this.$emit('closed');
      this.isOpen = false;
    },
    startDrag(event) {
      if (!this.$refs.dragContainer.contains(event.target)) return;
      if (this.noDrag) return;
      if (this.isResizing) return;
      if (!event.target.classList.contains('drag-handle')) {
        return;
      }
      this.isDragging = true;
      //  
      this.dragPosition.x = event.clientX - this.$refs.dragContainer.offsetLeft;
      this.dragPosition.y = event.clientY - this.$refs.dragContainer.offsetTop;

      document.addEventListener('mousemove', this.onDrag);
      document.addEventListener('mouseup', this.stopDrag);
    },
    stopDrag(event) {
      this.isDragging = false;

      //     
      this.startX = event.clientX - this.$refs.dragContainer.getBoundingClientRect().right;
      this.startY = event.clientY - this.$refs.dragContainer.getBoundingClientRect().bottom;

      document.removeEventListener('mousemove', this.onDrag);
      document.removeEventListener('mouseup', this.stopDrag);
    },
    onDrag(event) {
      // if (!this.$refs.dragContainer.contains(event.target)) return;
      if (!this.isDragging) return;
      // console.info('onDrag');
      //   
      const x = event.clientX - this.dragPosition.x;
      const y = event.clientY - this.dragPosition.y;
      this.$refs.dragContainer.style.left = `${x}px`;
      this.$refs.dragContainer.style.top = `${y}px`;
      this.$refs.dragContainer.style.width = `${this.$refs.dragContainer.offsetWidth}px`;
      this.$refs.dragContainer.style.height = `${this.$refs.dragContainer.offsetHeight}px`;
    },
    startResize(event, resizeDirection = 'bottomRight') {
      if (!this.$refs.dragContainer.contains(event.target)) return;
      if (this.noResize) return;
      if (this.isDragging) return;
      // console.info('startResize', resizeDirection);
      this.isResizing = true;
      this.$refs.dragContainer.classList.add('user-select-none');
      this.resizeDirection = resizeDirection;
      this.startWidth = this.$refs.dragContainer.offsetWidth;
      this.startHeight = this.$refs.dragContainer.offsetHeight;
      this.startX = event.clientX;
      this.startY = event.clientY;

      document.addEventListener('mousemove', this.onResize);
      document.addEventListener('mouseup', this.stopResize);
    },
    onResize(event) {
      // if (!this.$refs.dragContainer.contains(event.target)) return;
      if (!this.isResizing) return;
      // let newWidth, newHeight, newX, newY;
      const deltaX = event.clientX - this.startX;
      const deltaY = event.clientY - this.startY;
      const $container = this.$refs.dragContainer;

      $container.style.left = `${$container.offsetLeft}px`;
      $container.style.top = `${$container.offsetTop}px`;

      let newWidth, newHeight;
      const minWidth = 350;
      const minHeight = 210;
      // const minWidth = 200;
      // const minHeight = 180;
      // width, height  200 180

      switch (this.resizeDirection) {
        case 'bottomRight':
          $container.style.width = `${this.startWidth + deltaX}px`;
          $container.style.height = `${this.startHeight + deltaY}px`;
          break;
        case 'topLeft':
          //   :      
          newWidth = Math.max(this.startWidth - deltaX, minWidth);
          newHeight = Math.max(this.startHeight - deltaY, minHeight);
          $container.style.width = `${Math.max(this.startWidth - deltaX, minWidth)}px`;
          $container.style.height = `${Math.max(this.startHeight - deltaY, minHeight)}px`;
          if (newWidth !== minWidth) {
            $container.style.left = `${this.startX + deltaX}px`;
          }
          if (newHeight !== minHeight) {
            $container.style.top = `${this.startY + deltaY}px`;
          }
          break;
        case 'bottomLeft':
          //   :      ,  
          newWidth = Math.max(this.startWidth - deltaX, minWidth);
          newHeight = Math.max(this.startHeight + deltaY, minHeight);
          $container.style.width = `${newWidth}px`;
          $container.style.height = `${newHeight}px`;
          if (newWidth !== minWidth) {
            $container.style.left = `${this.startX + deltaX}px`;
          }
          break;
        case 'topRight':
          //  :      ,  
          newWidth = Math.max(this.startWidth + deltaX, minWidth);
          newHeight = Math.max(this.startHeight - deltaY, minHeight);
          $container.style.width = `${newWidth}px`;
          $container.style.height = `${newHeight}px`;
          if (newHeight !== minHeight) {
            $container.style.top = `${this.startY + deltaY}px`;
          }
          break;
        case 'top':
          newHeight = Math.max(this.startHeight - deltaY, minHeight);
          $container.style.height = `${newHeight}px`;
          if (newHeight !== minHeight) {
            $container.style.top = `${this.startY + deltaY}px`;
          }
          break;
        case 'left':
          newWidth = Math.max(this.startWidth - deltaX, minWidth);
          $container.style.width = `${newWidth}px`;
          if (newWidth !== minWidth) {
            $container.style.left = `${this.startX + deltaX}px`;
          }
          break;
        case 'bottom':
          newHeight = Math.max(this.startHeight + deltaY, minHeight);
          $container.style.height = `${newHeight}px`;
          break;
        case 'right':
          newWidth = Math.max(this.startWidth + deltaX, minWidth);
          $container.style.width = `${newWidth}px`;
          break;
        default:
          break;
      }
    },
    stopResize() {
      // console.info('stopResize');
      this.isResizing = false;
      this.$refs.dragContainer.classList.remove('user-select-none');
      document.removeEventListener('mousemove', this.onResize);
      document.removeEventListener('mouseup', this.stopResize);
    },
  },
  mounted() {},
};
</script>

<style scoped lang="scss">
.sc-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 0, 0, 0.5);

  &.transparent {
    background-color: transparent;
  }

  &.is-alert {
    z-index: 999; //   
    .sc-modal-container {
      min-width: 430px;
      width: fit-content;
      max-width: 900px;
      max-height: calc(100% - 60px);
      .sc-modal-head {
        button:first-of-type {
          display: none;
        }
      }
      .sc-modal-body {
        width: 100%;
        padding: 20px;
      }
    }
  }

  &.no-drag .drag-handle {
    cursor: default;
  }
  &.no-resize .resize-handle {
    cursor: default;
  }
  &.no-resize .sc-modal-container {
    cursor: default;
    .sc-modal-drag-handler {
      display: none;
    }
  }
  // 20240523_ X .
  // 1.  select-layer       .
  // 2.        .
  &.no-scroll {
    .sc-modal-container {
      overflow: unset;
      .sc-modal-body {
        overflow-y: unset;
      }
    }
  }
  &.blue-tit {
    .sc-modal-container {
      //border: 1px solid $SC-COLOR-WHITE;
      overflow: unset;
      .sc-modal-head {
        background-color: $SC-PRIMARY-COLOR-BLUE;
        span {
          color: $SC-COLOR-WHITE;
          font-weight: 300;
        }
        svg {
          color: $SC-COLOR-WHITE;
        }
      }
    }
  }
}

.sc-modal-container {
  position: absolute;
  overflow: hidden; //  select       .
  display: flex;
  flex-direction: column;
  background-color: $SC-COLOR-WHITE;
  min-height: 210px;

  &:has(.overflow) {
    overflow: visible;
  }
  &.transparent {
    border: 1px solid $SC-COLOR-GRAY-20;
  }

  &.full {
    top: 30px;
    bottom: 30px;
    left: 30px;
    right: 30px;
  }

  &.large {
    width: 1520px;
    height: 800px;
  }

  &.medium {
    width: 950px;
    height: 600px;
  }

  &.small {
    width: 450px;
    height: 400px;
  }

  .sc-modal-head {
    min-height: 50px;
    background-color: #f1f1f1;
    padding: 0 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #f3f3f3;
  }

  .sc-modal-body {
    flex-grow: 1;
    overflow-y: auto; //  select       .
    padding: 20px 20px 10px 30px;
    width: calc(100% - 10px); //      
    word-break: break-all;
    text-align: justify;

    &.padding-bottom {
      padding-bottom: 10px; // footer height
      margin-bottom: 53px; // footer height + border top 1px
    }

    :deep(.footer) {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background: $SC-COLOR-WHITE;
      border-top: 1px solid $SC-COLOR-GRAY-10;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding: 10px 30px;
    }
  }

  .sc-modal-foot {
    border-top: 1px solid #f3f3f3;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 30px;
  }
  .sc-modal-drag-handler {
    position: absolute;
    right: 1px;
    bottom: 1px;
    background: url('@/assets/img/svg/ico-drag-handler.svg');
    width: 8px;
    height: 8px;
  }
}
.drag-handle {
  cursor: move;
}
.resize-handle {
  position: absolute;
  width: 10px;
  height: 10px;
  &.top-right {
    cursor: nesw-resize;
    right: 0;
    top: 0;
  }
  &.bottom-right {
    cursor: nwse-resize;
    right: 0;
    bottom: 0;
  }
  &.top-left {
    cursor: nwse-resize;
    left: 0;
    top: 0;
  }
  &.bottom-left {
    cursor: nesw-resize;
    left: 0;
    bottom: 0;
  }
  &.top {
    cursor: ns-resize;
    left: 10px;
    right: 10px;
    top: 0;
    height: 10px;
    width: calc(100% - 20px);
  }
  &.left {
    cursor: ew-resize;
    top: 10px;
    bottom: 10px;
    left: 0;
    right: 10px;
    width: 10px;
    height: calc(100% - 20px);
  }
  &.bottom {
    cursor: ns-resize;
    left: 10px;
    right: 10px;
    bottom: 0;
    height: 10px;
    width: calc(100% - 20px);
  }
  &.right {
    cursor: ew-resize;
    top: 10px;
    bottom: 10px;
    right: 0;
    width: 10px;
    height: calc(100% - 20px);
  }
}
.user-select-none {
  user-select: none;
}
</style>
