<template>
  <div>
    <sc-detail-tab-box @click="onClick">
      <sc-detail-tab-content label="Preview" :idx="1">       
        <!-- Basig Usage -->
        <sc-sample-box title="Basig Usage">
          <template #description> init-show:  open   </template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <sc-tab-box @click="onClick">
                  <sc-tab-content label="TAB 1" :idx="1"> 1 </sc-tab-content>
                  <sc-tab-content label="TAB 2" :idx="2"> 2 </sc-tab-content>
                  <sc-tab-content label="TAB 3" :idx="3"> 3 </sc-tab-content>
                  <sc-tab-content label="TAB     Tooltip " :idx="4"
                    > 4 </sc-tab-content
                  >
                  <!-- Tab label   Custom -->
                  <sc-tab-label :idx="5">
                    <span style="color: yellowgreen; font-weight: bold">Custom  </span>
                  </sc-tab-label>
                  <sc-tab-content label="TAB 5" :idx="5"> 5 </sc-tab-content>
                </sc-tab-box>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> Tab Idx : {{ selectedTabId }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
              template='<sc-tab-box @click="onClick">
  <sc-tab-content label="TAB 1" :idx="1"> 1 </sc-tab-content>
  <sc-tab-content label="TAB 2" :idx="2"> 2 </sc-tab-content>
  <sc-tab-content label="TAB 3" :idx="3"> 3 </sc-tab-content>
  <sc-tab-content label="TAB     Tooltip " :idx="4"> 4 </sc-tab-content>
  <!-- Tab label   Custom -->
  <sc-tab-label :idx="5">
    <span style="color: yellowgreen; font-weight: bold">Custom  </span>
  </sc-tab-label>
  <sc-tab-content label="TAB 5" :idx="5"> 5 </sc-tab-content>
</sc-tab-box>

<div class="mt-5 text-sm"> Tab Idx : {{ selectedTabId }}</div>'
                />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //Basig Usage -->
        <!-- init-idx    -->
        <sc-sample-box title="init-idx   ">
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <sc-tab-box init-idx="2">
                  <sc-tab-content label="TAB 1" :idx="1"> 1 </sc-tab-content>
                  <sc-tab-content label="TAB 2" :idx="2"> 2 </sc-tab-content>
                  <sc-tab-content label="TAB 3" :idx="3"> 3 </sc-tab-content>
                </sc-tab-box>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> Tab Idx : {{ selectedTabId }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
              template='<sc-tab-box init-idx="2">
  <sc-tab-content label="TAB 1" :idx="1"> 1 </sc-tab-content>
  <sc-tab-content label="TAB 2" :idx="2"> 2 </sc-tab-content>
  <sc-tab-content label="TAB 3" :idx="3"> 3 </sc-tab-content>
</sc-tab-box>'
                />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //init-idx    -->
        <!-- navigation -->
        <sc-sample-box title="navigation">
          <template #description>  (  ,  )    .</template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <sc-tab-box>
                  <sc-tab-content
                    v-for="(item, idx) in tabSize"
                    :key="idx"
                    :label="`TAB Navigation ${idx + 1}`"
                    :idx="idx"
                    > {{ idx + 1 }} 
                  </sc-tab-content>
                </sc-tab-box>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Events</div>
                  <div class="box">
                    <sc-button @click="() => tabSize++"> </sc-button>
                    <sc-button @click="() => (tabSize = Math.max(0, --tabSize))"> </sc-button>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
              template='<div class="mb-5">
  <sc-button @click="() => tabSize++"> </sc-button>
  <sc-button @click="() => (tabSize = Math.max(0, --tabSize))"> </sc-button>
</div>
<sc-tab-box>
  <sc-tab-content
    v-for="(item, idx) in tabSize"
    :key="idx"
    :label="`TAB Navigation ${idx + 1}`"
    :idx="idx"
  >
     {{ idx + 1 }}
  </sc-tab-content>
</sc-tab-box>'
                />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //navigation -->
      </sc-detail-tab-content>
    </sc-detail-tab-box>
  </div>
</template>

<script>
import ScSampleBox from '@/components/common/ScSampleBox.vue';
import ScTabBox from '@/components/common/ScTabBox.vue';
import ScTabContent from '@/components/common/ScTabContent.vue';
import ScTabLabel from '@/components/common/ScTabLabel.vue';
import ScCodeHighlight from '@/components/common/ScCodeHighlight.vue';
import ScButton from '@/components/common/ScButton.vue';
import ScDetailTabBox from '@/components/layout/ScDetailTabBox.vue';
import ScDetailTabContent from '@/components/layout/ScDetailTabContent.vue';
import ScDetailAccordionWrapper from '@/components/layout/ScDetailAccordionWrapper.vue';
import ScDetailAccordion from '@/components/layout/ScDetailAccordion.vue';

export default {
  name: 'SampleTab',
  components: { ScSampleBox, ScTabBox, ScTabContent, ScTabLabel, ScCodeHighlight, ScButton, ScDetailTabBox, ScDetailTabContent, ScDetailAccordionWrapper, ScDetailAccordion },
  data() {
    return {
      selectedTabId: 1,
      tabSize: 10,
    };
  },
  methods: {
    onClick(idx) {
      this.selectedTabId = idx;
    },
  },
};
</script>

<style scoped lang="scss"></style>
