<template>
  <div>
    <sc-detail-tab-box @click="onClick">
      <sc-detail-tab-content label="Preview" :idx="1">       
        <!-- Basig Usage -->
        <sc-sample-box>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="w-[150px] mt-5 border">
                  <sc-text-field placeholder="" v-model="value" />
                </div>
                <div class="w-[150px] mt-5">
                  <sc-text-field placeholder="" v-model="value" rounded />
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ value }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
              template='
  <sc-text-field placeholder="" v-model="value" />
  <sc-text-field placeholder="" v-model="value" rounded />'
                />
            </ScDetailAccordion>
            <ScDetailAccordion title="Script" >
              <sc-code-highlight
              script="export default {
  data() {
    return {
      value: '',
    };
  },
};"
                />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //Basig Usage -->
        <!-- icon -->
        <sc-sample-box title="icon">
          <template #description>x  esc  clear .<br />   enter  @search  .</template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="w-[150px] mt-5">
                  <sc-text-field
                    placeholder=""
                    v-model="text"
                    clearable
                    searchable
                    @search="onSearch"
                  />
                </div>
                <div class="w-[150px] mt-5">
                  <sc-text-field
                    placeholder=""
                    v-model="text"
                    clearable
                    searchable
                    small
                    rounded
                    @search="onSearch"
                  />
                </div>
                <div class="w-[150px] mt-5">
                  <sc-text-field
                    placeholder=""
                    v-model="text"
                    clearable
                    searchable
                    medium
                    @search="onSearch"
                  />
                </div>
                <div class="w-[150px] mt-5">
                  <sc-text-field
                    placeholder=""
                    v-model="text"
                    clearable
                    searchable
                    medium
                    rounded
                    @search="onSearch"
                  />
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ text }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
              template='
<sc-text-field
  placeholder=""
  v-model="text"
  clearable
  searchable
  @search="onSearch"
/>
<sc-text-field
  placeholder=""
  v-model="text"
  clearable
  searchable
  small
  rounded
  @search="onSearch"
/>
<sc-text-field
  placeholder=""
  v-model="text"
  clearable
  searchable
  medium
  @search="onSearch"
/>
<sc-text-field
  placeholder=""
  v-model="text"
  clearable
  searchable
  medium
  rounded
  @search="onSearch"
/>
'
              />
            </ScDetailAccordion>
            <ScDetailAccordion title="Script" >
              <sc-code-highlight
              script="export default {
  data() {
    return {
      text: '',
    };
  },
  methods: {
    onSearch() {
      console.log('  =>', this.text);
    },
  },
};"
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //icon -->
        <!-- disabled  -->
        <sc-sample-box title="disabled ">
          <template #description>x  esc  clear .<br />   enter  @search  .</template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="w-[150px] mt-5">
                  <sc-text-field placeholder="" v-model="text" disabled />
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ text }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
                template='<sc-text-field placeholder="" v-model="text" disabled />'
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //disabled  -->
        <!-- readonly  -->
        <sc-sample-box title="readonly ">
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="w-[150px] mt-5">
                  <sc-text-field placeholder="" v-model="text" readonly />
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
                template='<sc-text-field placeholder="" v-model="text" readonly />'
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //readonly  -->
        <!-- mask -->
        <sc-sample-box title="mask">          
          <template #description>Mask . #(), A(), a(), N(/), n(/)   .<br /> preset number, english, korean  /  /     .</template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <sc-form-box col="1">
                  <sc-form-item label=" mask='number'">
                    <div class="flex items-center gap-5">
                      <sc-text-field placeholder="" v-model="mask.number" mask="number" />
                      <div class="text-sm"> : {{ mask.number }}</div>
                    </div>
                  </sc-form-item>
                  <sc-form-item label="/ mask='english'">
                    <div class="flex items-center gap-5">
                      <sc-text-field placeholder=", " v-model="mask.english" mask="english" />
                      <div class="text-sm"> : {{ mask.english }}</div>
                    </div>
                  </sc-form-item>
                  <sc-form-item label="/ mask='korean'">
                    <div class="flex items-center gap-5">
                      <sc-text-field placeholder=", " v-model="mask.korean" mask="korean" />
                      <div class="text-sm"> : {{ mask.korean }}</div>
                    </div>
                  </sc-form-item>
                  <sc-form-item label="<br/>mask='/[0-9]+/'">
                    <div class="flex items-center gap-5">
                      <sc-text-field
                        placeholder="  /[0-9]+/"
                        v-model="mask.value1"
                        mask="/[0-9]+/"
                      />
                      <div class=""> : {{ mask.value1 }}</div>
                    </div>
                  </sc-form-item>
                  <sc-form-item label="<br/>mask='####'">
                    <div class="flex items-center gap-5">
                      <sc-text-field placeholder="  4" v-model="mask.value2" mask="####" />
                      <div class="text-sm"> : {{ mask.value2 }}</div>
                    </div>
                  </sc-form-item>
                  <sc-form-item label="<br/>mask='aAaAaA'">
                    <div class="flex items-center gap-5">
                      <sc-text-field
                        placeholder=", , , , , "
                        v-model="mask.value3"
                        mask="aAaAaA"
                      />
                      <div class=""> : {{ mask.value3 }}</div>
                    </div>
                  </sc-form-item>
                  <sc-form-item label="<br/>mask=###aaa">
                    <div class="flex items-center gap-5">
                      <sc-text-field placeholder="3,3" v-model="mask.value4" mask="###aaa" />
                      <div class="text-sm"> : {{ mask.value4 }}</div>
                    </div>
                  </sc-form-item>
                </sc-form-box>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template">
              <sc-code-highlight
              template='
<sc-text-field placeholder="" v-model="mask.number" mask="number" />
<sc-text-field placeholder=", " v-model="mask.english" mask="english" />
<sc-text-field placeholder=", " v-model="mask.korean" mask="korean" />
<sc-text-field placeholder="  /[0-9]+/" v-model="mask.value1" mask="/[0-9]+/" />
<sc-text-field placeholder="  4" v-model="mask.value2" mask="####" />
<sc-text-field placeholder=", , , , , " v-model="mask.value3" mask="aAaAaA" />
<sc-text-field placeholder="3,3" v-model="mask.value4" mask="###aaa" />
'
              />
            </ScDetailAccordion>
            <ScDetailAccordion title="Script">
              <sc-code-highlight
              script="export default {
  data() {
    return {
      mask: {
        number: '',
        english: '',
        korean: '',
        value1: '',
        value2: '',
        value3: '',
        value4: '',
      },
    };
  },
};"
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //mask -->
      </sc-detail-tab-content>
    </sc-detail-tab-box>
  </div>
</template>

<script>
import ScTextField from '@/components/common/ScTextField.vue';
import ScSampleBox from '@/components/common/ScSampleBox.vue';
import ScCodeHighlight from '@/components/common/ScCodeHighlight.vue';
import ScFormBox from '@/components/common/ScFormBox.vue';
import ScFormItem from '@/components/common/ScFormItem.vue';
import ScDetailTabBox from '@/components/layout/ScDetailTabBox.vue';
import ScDetailTabContent from '@/components/layout/ScDetailTabContent.vue';
import ScDetailAccordionWrapper from '@/components/layout/ScDetailAccordionWrapper.vue';
import ScDetailAccordion from '@/components/layout/ScDetailAccordion.vue';

export default {
  name: 'SampleTextField',
  components: { ScTextField, ScFormBox, ScFormItem, ScSampleBox, ScCodeHighlight, ScDetailTabBox, ScDetailTabContent, ScDetailAccordionWrapper, ScDetailAccordion },
  data() {
    return {
      mask: {
        number: '',
        english: '',
        korean: '',
        value1: '',
        value2: '',
        value3: '',
        value4: '',
      },
      value: '',
      text: '',
    };
  },
  methods: {
    onSearch() {
      console.info('  =>', this.text);
    },
  },
};
</script>

<style scoped lang="scss"></style>
