import { useI18n } from "vue-i18n";
import { isEmpty, inRange } from "lodash";

export function useRules() {
  // t    
  const { t } = useI18n();

  const ruleRequired = (val) => {
    return val === 0 || !val || t("ISPEC_MSG_10101"); //.
  };
  const ruleNotEmpty = (val) => {
    return !isEmpty(val) || t("ISPEC_MSG_10101"); //.
  };
  const ruleEmail = (val) => {
    return (
      !(
        val &&
        !/^[a-za-z]([-_.]?[0-9a-za-z])*@[0-9a-za-z]([-_.]?[0-9a-za-z])*\.[a-za-z]{1,3}$/.test(
          val
        )
      ) || t("ISPEC_MSG_10102") //    .
    );
  };

  const ruleNumeric = (val) => {
    return !isNaN(val) || t("ISPEC_MSG_10103"); //  .
  };

  const ruleBetween = (val, min, max) => {
    return (val) =>
      inRange(val, min, max) ||
      val * 1 === max ||
      t("ISPEC_MSG_10104", [min, max]); // ${min}~${max}    .
  };

  const ruleMaxLength = (val, length) => {
    return (val) => val.length <= length || t("ISPEC_MSG_10105", [length]); //  ${length}   .
  };

  const ruleMinLength = (val, length) => {
    return (val) => val.length >= length || t("ISPEC_MSG_10106", [length]); //  ${length}  .
  };

  const ruleMaxByte = (val, length) => {
    return (val) => {
      val = val || "";
      const byteLength = val
        .split("")
        .map((s) => s.charCodeAt(0))
        .reduce((prev, c) => prev + (c === 10 ? 2 : c >> 7 ? 2 : 1), 0);

      return (
        byteLength <= length ||
        t("ISPEC_MSG_10122", [
          length.tostring().replace(/\b(?=(\d{3})+(?!\d))/g, ","),
        ])
      ); //  {0} byte   .
    };
  };

  return {
    ruleRequired,
    ruleNotEmpty,
    ruleEmail,
    ruleNumeric,
    ruleBetween,
    ruleMaxLength,
    ruleMinLength,
    ruleMaxByte,
  };
}
