<template>
  <div>
    <sc-sample-box>
      <template #description>  .</template>
      <div class="w-[150px]">
        <sc-text-field v-model="text" />
        <sc-button class="mt-2" @click="copyText"></sc-button>
      </div>

      <sc-code-highlight
        template='<sc-text-field v-model="text" />
<sc-button class="mt-2" @click="copyText"></sc-button>'
        script="export default {
          methods: {
            copyText() {
              this.$util.copyToClipboard(this.text, () => {
                this.$util.alert('  ');
              });
            },
          },
          data() {
            return {
              text: '',
            };
          },
        };"
      />
    </sc-sample-box>

    <sc-sample-box>
      <sc-button @click="copyUrl">URL </sc-button>
      <sc-code-highlight
        template='<sc-button @click="copyUrl">URL </sc-button>'
        script="export default {
          methods: {
            copyUrl() {
              this.$util.copyToClipboard(window.location.href, () => {
                this.$util.alert(' .<br>  (Ctrl + v) ');
              });
            },
          },
        };"
      />
    </sc-sample-box>
  </div>
</template>

<script>
import ScSampleBox from '@/components/common/ScSampleBox.vue';
import ScTextField from '@/components/common/ScTextField.vue';
import ScButton from '@/components/common/ScButton.vue';
import ScCodeHighlight from '@/components/common/ScCodeHighlight.vue';

export default {
  name: 'SampleClipboard',
  components: { ScButton, ScCodeHighlight, ScSampleBox, ScTextField },
  methods: {
    copyText() {
      this.$util.copyToClipboard(this.text, () => {
        this.$util.alert('  ');
      });
    },
    copyUrl() {
      this.$util.copyToClipboard(window.location.href, () => {
        this.$util.alert(' .<br>  (Ctrl + V) .');
      });
    },
  },
  data() {
    return {
      text: '',
    };
  },
};
</script>

<style scoped lang="scss"></style>
