<template>
  <div>
    <sc-sample-box>
      <template #description>this.$util.confirm('', confirm, cancelFn, closeFn);</template>
      <div>
        <sc-button @click="showConfirm"></sc-button>
      </div>
      <div class="mt-5 text-sm"> : {{ result }}</div>

      <sc-code-highlight
        template='<div>
  <sc-button @click="showConfirm"></sc-button>
</div>
<div class="mt-5 text-sm"> : {{ result }}</div>'
        script="export default {
    data() {
      return: {
        result: '',
      }
    };
  },
  methods: {
    showConfirm() {
      this.$util.confirm(
        '?',
        () => {
          //   
          this.result = ' ';
        },
        () => {
          //   
          this.result = ' ';
        },
        () => {
          //  ('X')  
          this.result = ' ';
        },
      );
    },
  },
};"
      >
        <div>
          this.$util.confirm('', confirmCallBack, cancelCallBack, closeCallBack)
          <div class="mt-5">
            <p>() message :  .</p>
            <p>() confirmCallBack :    function.</p>
            <p>() cancelCallBack :    function</p>
            <p>() closeCallBack :  ('X')   function.</p>
          </div>
        </div>
      </sc-code-highlight>
    </sc-sample-box>
  </div>
</template>

<script>
import ScButton from '@/components/common/ScButton.vue';
import ScSampleBox from '@/components/common/ScSampleBox.vue';
import ScCodeHighlight from '@/components/common/ScCodeHighlight.vue';

export default {
  name: 'SampleConfirm',
  components: { ScButton, ScCodeHighlight, ScSampleBox },
  data() {
    return {
      result: '',
    };
  },
  methods: {
    showConfirm() {
      this.$util.confirm(
        '?',
        () => {
          // 
          this.result = ' ';
        },
        () => {
          // 
          this.result = ' ';
        },
        () => {
          this.result = ' ';
        },
      );
    },
  },
};
</script>

<style scoped lang="scss"></style>
