<template>
  <teleport to="body">
    <sc-modal
      ref="alertModal"
      :title="title"
      @closed="closeIcon"
      transparent
      is-alert
      no-drag
      no-resize
    >
      <div v-html="message"></div>
      <template v-slot:action>
        <sc-button
          ref="closeButton"
          @click="close"
          @keydown.enter="close"
          color="indigo"
          size="medium"
        >
          
        </sc-button>
      </template>
    </sc-modal>
  </teleport>
</template>

<script>
import ScButton from '@/components/common/ScButton.vue';
import ScModal from '@/components/common/ScModal.vue';

export default {
  name: 'ScAlert',
  components: { ScModal, ScButton },
  data() {
    return {
      isShow: false,
      message: '',
      callbackFn: () => {},
      title: this.$t(''),
    };
  },
  methods: {
    show(message, callbackFn, title) {
      // console.info('scalert.vue show method');
      this.message = message;
      this.callbackFn = callbackFn;
      if (this.$util.isNotEmpty(title)) this.title = title;
      else this.title = this.$t('');
      this.isShow = true;
      this.$refs.alertModal.open();
      // console.info("window.addEventListener('popstate')");
      window.addEventListener('popstate', this.handlePopState);
      //    
      window.addEventListener('keydown', this.handleEnterKey);
      //      
      this.$nextTick(() => {
        this.$refs.closeButton.$el.focus();
      });
    },
    handleEnterKey(event) {
      if (event.key === 'Enter' && this.isShow) {
        this.close();
      }
    },
    closeIcon() {
      this.isShow = false;
      // console.info("window.removeEventListener('popstate')");
      window.removeEventListener('popstate', this.handlePopState);
      window.removeEventListener('keydown', this.handleEnterKey); //   
      this.$nextTick(() => {
        this.callbackFn();
      });
    },
    close() {
      // this.closeIcon();
      this.$refs.alertModal.close();
      // console.info("window.removeEventListener('popstate')");
      window.removeEventListener('popstate', this.handlePopState);
      window.removeEventListener('keydown', this.handleEnterKey); //   
    },
    init() {
      // console.info('init');
      this.isShow = false;
      this.message = '';
      this.callbackFn = () => {};
      this.$refs.alertModal.close();
      window.removeEventListener('keydown', this.handleEnterKey); //   
    },
    handlePopState() {
      //     
      //   
      // console.info('handlePopState');
      this.init();
    },
  },
  mounted() {
    // window.addEventListener('popstate', this.handlePopState);
    // console.info('window.addEventListener(\'popstate\')');
  },
  beforeUnmount() {
    window.removeEventListener('popstate', this.handlePopState);
    window.removeEventListener('keydown', this.handleEnterKey); //      
    console.info("window.removeEventListener('popstate')");
  },
};
</script>

<style scoped lang="scss"></style>
