<template>
  <div>
    <sc-detail-tab-box @click="onClick">
      <sc-detail-tab-content label="Preview" :idx="1">
        <!-- Basig Usage -->
        <sc-sample-box>
          <template #description> init-show:  open   </template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="h-40">
                  <div class="w-[150px] float-left">
                    <sc-dropdown :items="simpleItems" v-model="simpleValue" @change="onChange" />
                  </div>
                  <div class="w-[150px] float-left ml-2.5">
                    <sc-dropdown :items="simpleItems" v-model="simpleValue" disabled />
                  </div>
                  <div class="w-[150px] float-left ml-2.5">
                    <sc-dropdown :items="simpleItems" v-model="simpleValue" medium plaveholder="Select!" />
                  </div>
                  <div class="w-[150px] float-left ml-2.5">
                    <sc-dropdown :items="simpleItems" v-model="simpleValue" size="medium" disabled />
                  </div>
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ simpleValue }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
              template='<div class="w-[150px] float-left">
  <sc-dropdown :items="simpleItems" v-model="simpleValue" @change="onChange" />
</div>
<div class="w-[150px] float-left ml-2.5">
  <sc-dropdown :items="simpleItems" v-model="simpleValue" disabled />
</div>
<div class="w-[150px] float-left ml-2.5">
  <sc-dropdown :items="simpleItems" v-model="simpleValue" medium plaveholder="Select!" />
</div>
<div class="w-[150px] float-left ml-2.5">
  <sc-dropdown :items="simpleItems" v-model="simpleValue" size="medium" disabled />
</div>
<p class="list"> : {{ simpleValue }}</p>'
                />
            </ScDetailAccordion>
            <ScDetailAccordion title="Script" >
              <sc-code-highlight
              script="export default {
  methods: {
    onChange(val) {
      console.info('onChange', val);
    },
  },
  data() {
    return {
      simpleItems: ['Utility-First Foundamentals', 'Hover, Focus, and Other States', 'Responsive Design', 'Dark Mode', 'Reusing Styles'],
      simpleValue: '',
    };
  },
};"
                />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //Basig Usage -->
        <!-- defaultValue  option  -->
        <sc-sample-box title="defaultValue  option ">
          <template #description>  dropdown  ALL     .    .  dafault-value  ''      .</template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="h-40">
                  <div class="w-[150px] float-left">
                    <!-- option 'ALL'  . value '' String -->
                    <sc-dropdown :items="simpleItems" v-model="dfValue1" placeholder="ALL" default-value />
                  </div>
                  <div class="w-[150px] float-left ml-2.5">
                    <!-- option ''  . value 'all' -->
                    <sc-dropdown :items="items" v-model="dfValue2" placeholder="" default-value="all" />
                  </div>
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ dfValue1 }}</p>
                    <p class="list"> : {{ dfValue2 }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
                template='<div class="w-[150px] float-left">
  <sc-dropdown :items="simpleItems" v-model="dfValue1" placeholder="ALL" default-value />
</div>
<div class="w-[150px] float-left ml-2.5">
  <sc-dropdown :items="items" v-model="dfValue2" placeholder="" default-value="all" />
</div>
<p class="list"> : {{ dfValue1 }}</p>
<p class="list"> : {{ dfValue2 }}</p>'
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //defaultValue  option  -->
        <!-- borderless -->
        <sc-sample-box title="borderless">
          <template #description> </template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="h-32">
                  <div class="w-[150px] float-left">
                    <sc-dropdown :items="pageItems" v-model="pageValue" borderless />
                  </div>
                  <div class="w-[150px] float-left ml-2.5">
                    <sc-dropdown :items="pageItems" v-model="pageValue" small borderless />
                  </div>
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ pageValue }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
                template='<div class="w-[150px] float-left">
  <sc-dropdown :items="pageItems" v-model="pageValue" borderless />
</div>
<div class="w-[150px] float-left ml-2.5">
  <sc-dropdown :items="pageItems" v-model="pageValue" small borderless />
</div>
<p class="list"> : {{ pageValue }}</p>'
              />
            </ScDetailAccordion>
            <ScDetailAccordion title="Script" >
              <sc-code-highlight
                script="export default {
  date() {
    return {
      pageItems: [
        { value: '100', text: '100' },
        { value: '300', text: '300' },
        { value: '500', text: '500' },
        { value: '1000', text: '1000' },
      ],
      pageValue: '100',
    };
  },
};"
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //borderless -->
        <!-- item  Object   -->
        <sc-sample-box title="item  Object  ">
          <template #description>Item   value   text select  .</template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="h-40">
                  <div class="w-[150px]">
                    <sc-dropdown :items="items" v-model="value" @change="onChange" />
                  </div>
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ value }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
                template='<div class="w-[150px]">
  <sc-dropdown :items="items" v-model="value" @change="onChange" />
</div>
<p class="list"> : {{ value }}</p>'
              />
            </ScDetailAccordion>
            <ScDetailAccordion title="Script" >
              <sc-code-highlight
              script="export default {
  date() {
    return {
      items: [
        { value: 'dropdown1', text: '1' },
        { value: 'dropdown2', text: '2' },
        { value: 'dropdown3', text: '3' },
        { value: 'dropdown4', text: '4' },
        { value: 'dropdown5', text: '5' },
      ],
      value: '',
    };
  },
};"
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //item  Object   -->
        <!-- item Custom Key  -->
        <sc-sample-box title="item Custom Key ">
          <template #description>   custom Object     .   key, code   name .</template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="h-28">
                  <div class="w-[150px]">
                    <sc-dropdown
                      :items="items2"
                      v-model="value2"
                      item-value="code"
                      item-text="name"
                      @change="onChange"
                    />
                  </div>
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ value2 }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
              template='<div class="w-[150px]">
  <sc-dropdown
    :items="items2"
    v-model="value2"
    item-value="code"
    item-text="name"
    @change="onChange"
  />
</div>
<p class="list"> : {{ value2 }}</p>'
/>
            </ScDetailAccordion>
            <ScDetailAccordion title="Script" >
              <sc-code-highlight
              script="export default {
  date() {
    return {
      items2: [
        { code: 'A', name: 'A CODE' },
        { code: 'B', name: 'B CODE' },
        { code: 'C', name: 'C CODE' },
      ],
      value2: '',
    };
  },
};"
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //item Custom Key  -->
        <!-- searchable -->
        <sc-sample-box title="searchable">
          <template #description>
            :searchable -     <br />
            -  Up, Down, Enter   <br />
            -      Enter 
          </template>
          <ScDetailAccordionWrapper>
            <ScDetailAccordion title="Demo" init-show>
              <template #demo>
                <div class="h-40">
                  <div class="w-[150px]">
                    <sc-dropdown
                      :items="simpleItems"
                      v-model="dfValue1"
                      searchable
                      default-value
                      @change="onChange"
                    />
                  </div>
                </div>
              </template>
              <template #demodescription>
                <div class="description-wrap">
                  <div class="title">Value</div>
                  <div class="box">
                    <p class="list"> : {{ dfValue1 }}</p>
                  </div>
                </div>
              </template>
            </ScDetailAccordion>
            <ScDetailAccordion title="Template" >
              <sc-code-highlight
              template='<div class="w-[150px]">
  <sc-dropdown
    :items="simpleItems"
    v-model="dfValue1"
    searchable
    default-value
    @change="onChange"
  />
</div>
<p class="list"> : {{ dfValue1 }}</p>'
/>
            </ScDetailAccordion>
            <ScDetailAccordion title="Script" >
              <sc-code-highlight
              script="export default {
  date() {
    return {
      simpleItems: ['item1', 'item2', 'item3', 'item4', 'item5', 'item6', 'item7'],
      dfValue1: '',
    };
  },
};"
              />
            </ScDetailAccordion>
          </ScDetailAccordionWrapper>
        </sc-sample-box>
        <!-- //searchable -->
      </sc-detail-tab-content>
    </sc-detail-tab-box>
  </div>
</template>

<script>
import ScSampleBox from '@/components/common/ScSampleBox.vue';
import ScDropdown from '@/components/common/ScDropdown.vue';
import ScCodeHighlight from '@/components/common/ScCodeHighlight.vue';
import ScDetailTabBox from '@/components/layout/ScDetailTabBox.vue';
import ScDetailTabContent from '@/components/layout/ScDetailTabContent.vue';
import ScDetailAccordionWrapper from '@/components/layout/ScDetailAccordionWrapper.vue';
import ScDetailAccordion from '@/components/layout/ScDetailAccordion.vue';

export default {
  name: 'SampleDropdown',
  components: { ScSampleBox, ScDropdown, ScCodeHighlight, ScDetailTabBox, ScDetailTabContent, ScDetailAccordionWrapper, ScDetailAccordion },
  methods: {
    onChange(val) {
      console.info('onChange : ', val);
    },
    onClick(idx) {
      this.selectedTabId = idx;
    },
  },
  data() {
    return {
      dfValue1: '',
      dfValue2: '',
      dfValue3: '',
      simpleItems: ['item1', 'item2', 'item3', 'item4', 'item5', 'item6', 'item7'],
      simpleValue: '',
      items: [
        { value: 'dropdown1', text: '1' },
        { value: 'dropdown2', text: '2' },
        { value: 'dropdown3', text: '3' },
        { value: 'dropdown4', text: '4' },
        { value: 'dropdown5', text: '5' },
      ],
      value: '',
      items2: [
        { code: 'A', name: 'A CODE' },
        { code: 'B', name: 'B CODE' },
        { code: 'C', name: 'C CODE' },
      ],
      value2: '',
      pageItems: [
        { value: '100', text: '100' },
        { value: '300', text: '300' },
        { value: '500', text: '500' },
        { value: '1000', text: '1000' },
      ],
      pageValue: '100',
      selectedTabId: 1,
    };
  },
};
</script>

<style scoped lang="scss"></style>
